/* Pricing Tables
------------------------------------------------------------ */
.mtphr-pricing-table {
  border: 1px solid #CDCDCD;
  padding: 9px;
  margin-bottom: 60px; }

.mtphr-pricing-table p,
.mtphr-pricing-table-title {
  text-align: center; }

.mtphr-pricing-table-title {
  font-size: 18px;
  text-transform: uppercase;
  line-height: 18px;
  padding: 10px 0; }

.mtphr-pricing-table-values,
.mtphr-pricing-table-per {
  margin-bottom: 20px; }

.mtphr-pricing-table-price {
  padding: 13px 0 10px 0; }

.mtphr-pricing-table-price span {
  font-weight: lighter; }

.mtphr-pricing-table-cents {
  vertical-align: top; }

.mtphr-pricing-table-per {
  font-size: 14px;
  line-height: 14px;
  font-style: italic; }

.mtphr-pricing-table-values p {
  margin: 0;
  padding: 8px 0 5px;
  border-bottom: 1px solid #CDCDCD; }

.mtphr-pricing-table-button {
  margin-bottom: 15px; }

.mtphr-pricing-table-button a {
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
  color: #FFF;
  background: #666666;
  padding: 10px 24px 8px 24px; }

.mtphr-pricing-table-button a:hover {
  color: #FFF;
  background-color: #EE2535; }

.mtphr-pricing-table-button {
  margin-bottom: 20px; }

.mtphr-pricing-table-button a {
  display: inline-block; }

/* Normal Style */
.mtphr-pricing-table-normal .mtphr-pricing-table-price {
  background: #666666;
  padding: 13px 0 10px 0;
  margin-bottom: 10px; }

.mtphr-pricing-table-normal:hover .mtphr-pricing-table-price {
  background: #EE2535; }

.mtphr-pricing-table-normal .mtphr-pricing-table-price span {
  color: #FFF; }

.mtphr-pricing-table-normal .mtphr-pricing-table-dollar {
  font-size: 60px;
  line-height: 50px;
  margin-right: 10px; }

.mtphr-pricing-table-normal .mtphr-pricing-table-cents {
  font-size: 30px;
  line-height: 30px;
  margin-right: -35px; }

/* Condensed Style */
.mtphr-pricing-table-condensed .mtphr-pricing-table-title {
  color: #FFF;
  background: #666666;
  padding-top: 13px;
  padding-bottom: 9px; }

.mtphr-pricing-table-condensed:hover .mtphr-pricing-table-title {
  background: #EE2535; }

.mtphr-pricing-table-condensed .mtphr-pricing-table-price {
  padding: 13px 0 10px 0;
  margin-bottom: 0;
  border-bottom: 1px solid #CDCDCD;
  padding-bottom: 15px; }

.mtphr-pricing-table-condensed .mtphr-pricing-table-dollar {
  font-size: 36px;
  line-height: 30px;
  margin-right: 8px; }

.mtphr-pricing-table-condensed .mtphr-pricing-table-cents {
  font-size: 20px;
  line-height: 20px; }

.mtphr-pricing-table-condensed .mtphr-pricing-table-per {
  display: block;
  color: #999;
  margin-bottom: 0; }

/* List Style */
.mtphr-pricing-table-list {
  border-left: none;
  border-right: none;
  border-bottom: none;
  padding: 0; }

.mtphr-pricing-table-list p {
  text-align: left; }

.mtphr-pricing-table-list .mtphr-pricing-table-values p strong {
  float: right;
  text-transform: uppercase;
  color: #EE2535; }

/* Post Sliders
------------------------------------------------------------ */
.mtphr-post-slider {
  position: relative;
  margin: 20px 0; }

.mtphr-post-slider-title {
  float: left; }

.mtphr-post-slider-navigation {
  float: right; }

.mtphr-post-slider .mtphr-post-slider-prev {
  margin-right: 10px; }

.mtphr-post-slider-content-wrapper {
  overflow: hidden; }

.mtphr-post-slider-block {
  float: left;
  width: 220px;
  margin-right: 20px;
  display: none; }

/* Slide Graph
------------------------------------------------------------ */
.mtphr-slide-graph {
  position: relative;
  padding-top: 20px;
  margin-bottom: 30px; }

.mtphr-slide-graph-title {
  position: absolute;
  left: 0;
  top: 19px;
  font-size: 14px;
  line-height: 14px; }

.mtphr-slide-graph-fill-bg {
  position: relative;
  width: 100%;
  height: 10px;
  background: #e3e6e8;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px; }

.mtphr-slide-graph-fill {
  position: absolute;
  height: 10px;
  background: #777;
  -webkit-border-top-left-radius: 5px;
  -webkit-border-bottom-left-radius: 5px;
  -moz-border-radius-topleft: 5px;
  -moz-border-radius-bottomleft: 5px;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px; }

.mtphr-slide-graph-fill[percent="100"] {
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px; }

.mtphr-slide-graph-percent {
  position: absolute;
  top: -18px;
  font-size: 14px;
  line-height: 14px;
  text-align: center;
  width: 100px;
  /* white-space: nowrap; */
  margin-left: -50px; }

/* Tabs
------------------------------------------------------------ */
.mtphr-tabs-container {
  width: 100%;
  background: #FFF;
  border: 1px solid #c8cdd1;
  border-spacing: 0;
  border-collapse: separate;
  margin-bottom: 30px; }

.mtphr-tabs-container td {
  padding: 0; }

.mtphr-tab-link {
