/* Toggles
------------------------------------------------------------ */
.mtphr-toggle {
  padding-bottom: 4px;
  margin-bottom: 7px;
  border-bottom: 1px solid #e3e6e8; }

.mtphr-toggle:last-child,
.mtphr-toggle.last {
  padding-bottom: 0;
  margin-bottom: 0;
  border: none; }

.mtphr-toggle-heading {
  position: relative;
  line-height: 30px;
  padding-left: 35px;
  margin: 0; }

.mtphr-toggle-content {
  display: none;
  padding-left: 35px; }

.mtphr-toggle-button {
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  width: 21px;
  height: 21px;
  font-size: 14px;
  text-align: center;
  line-height: 21px;
  background: #e3e6e8;
  margin-top: -12px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px; }

.mtphr-toggle-button-expanded {
  display: none; }

.mtphr-toggle-heading.active .mtphr-toggle-button-expanded {
  display: block; }

.mtphr-toggle-heading.active .mtphr-toggle-button-condensed {
  display: none; }

