  border-radius: 0 0 0 15px; }

.zebra tfoot td:last-child {
  -moz-border-radius: 0 0 15px;
  -webkit-border-radius: 0 0 15px 0;
  border-radius: 0 0 15px 0; }

.zebra tfoot td:only-child {
